<template>
  <div>
    <b-input-group style="width: 50rem; margin: 0 auto 3rem auto;" class="mt-3">
      <b-form-input
        aria-describedby="input-live-help input-live-feedback"
        size="lg"
        class="mr-sm-2"
        placeholder="Hae paikkakuntaa"
        v-model="text"
        @keydown.enter.prevent="getWeather(text)"
      ></b-form-input>
      <b-input-group-append>
        <b-button @click="getWeather(text)" size="md" variant="outline-primary">Haku</b-button>
      </b-input-group-append>
    </b-input-group>
    <div style="margin: 0 auto; max-width: 100rem;">
      <b-row>
        <b-col v-if="weather.name" lg>
          <b-container style="max-width: 40rem">
            <H4>Säätiedot nyt:</H4>
            <b-card class="mb-3" header-tag="header">
              <template v-slot:header>
                <b-row>
                  <b-col>
                    <div>
                      <h5 class="mt-3">{{weather.name}}</h5>
                    </div>
                  </b-col>
                  <b-col>
                    <div></div>
                  </b-col>
                  <b-col>
                    <div>
                      <b-img
                        :src="'http://openweathermap.org/img/w/' + weather.weather[0].icon + '.png' "
                        blank-color="#ccc"
                        fluid
                      ></b-img>
                    </div>
                  </b-col>
                </b-row>
              </template>
              <b-media>
                <b-row>
                  <b-col>
                    <div>Lämpötila: {{weather.main.temp}}c</div>
                    <div>Tuntuu kuin: {{weather.main.feels_like}}c</div>
                    <div>Minimi: {{weather.main.temp_min}}c</div>
                    <div>Maksimi: {{weather.main.temp_max}}c</div>
                    <div>Tuulen nopeus: {{weather.wind.speed}}m/s</div>
                  </b-col>
                  <b-col>
                    <div>Säätila: "{{weather.weather[0].description}}"</div>
                    <div>Pilvisyys: {{weather.clouds.all}}%</div>
                    <div>Aurinko nousee: {{sunrise}}</div>
                    <div>Aurinko laskee: {{sunset}}</div>
                  </b-col>
                </b-row>
              </b-media>
            </b-card>
          </b-container>
        </b-col>
        <b-col v-if="forecast.list" lg>
          <b-container style="max-width: 40rem">
            <H4>Tuleva sää:</H4>
            <div style="overflow-y: scroll; height: 40rem;">
              <b-card
                class="mb-4"
                header-tag="header"
                v-for="(res, index) in forecast.list"
                :key="index"
              >
                <template v-slot:header>
                  <b-row>
                    <b-col>
                      <div>
                        <h5 class="mt-3">{{res.dt_txt}}</h5>
                      </div>
                    </b-col>
                  </b-row>
                </template>
                <b-media>
                  <b-row>
                    <b-col>
                      <div>Lämpötila: {{res.main.temp}}c</div>
                      <div>Tuntuu kuin: {{res.main.feels_like}}c</div>
                      <div>Minimi: {{res.main.temp_min}}c</div>
                      <div>Maksimi: {{res.main.temp_max}}c</div>
                      <div>Tuulen nopeus: {{res.wind.speed}}m/s</div>
                    </b-col>
                    <b-col>
                      <div>Säätila: "{{res.weather[0].description}}"</div>
                      <div>Pilvisyys: {{res.clouds.all}}%</div>
                      <div>Aurinko nousee: {{sunrise}}</div>
                      <div>Aurinko laskee: {{sunset}}</div>
                    </b-col>
                  </b-row>
                </b-media>
              </b-card>
            </div>
          </b-container>
        </b-col>
      </b-row>
    </div>
  </div>
</template>
<script>
import axios from "@/plugins/axios"

export default {
  name: 'Weather',
  data () {
    return {
      text: '',
      weather: [],
      forecast: [],
      errors: [],
    }
  },
  methods: {
    getWeather (city) {
      axios.getResponse("weather", city).then(res => {
        this.weather = res.data;
      })
        .catch(err => {
          this.errors.push(err)
        })
      axios.getResponse("forecast", city).then(res => {
        this.forecast = res.data;
      })
        .catch(err => {
          this.errors.push(err)
        })
      this.text = ''
    }
  },
  computed: {
    sunrise () {
      var date = new Date(1000 * this.weather.sys.sunrise)
      var hours = date.getHours()
      var minutes = date.getMinutes();
      return hours + ":" + minutes
    },
    sunset () {
      var date = new Date(1000 * this.weather.sys.sunset)
      var hours = date.getHours()
      var minutes = date.getMinutes();
      return hours + ":" + minutes
    }
  },
  mounted () {
    this.getWeather("helsinki")
  }
}
</script>
<style scoped>
.main {
  text-align: center;
}
</style>